<template>
    <v-header>
        <div slot="right" :class="['h-right']"><a v-link="{name:'certificate-add',query:{isNew:true}}"
                                                  class="mg-r10">添加</a></div>
    </v-header>
    <v-content>
        <ul class="section" v-for="item in items" v-link="{name:'certificate-show',params:{id:item.id}}">
            <li class="matter">
                <div class="certificate-box">
                    <span :class="['img','thumb']"></span>
                    <span :class="['name',item.status==3 ? 'color3':'']">{{item.name}}</span>
                    <div @click="showRemark($event,item.status)" style="float: right;margin-right: .1rem;">
                        <span class="status color{{item.status}}">{{item.statusName}}</span>
                        <span class="img help" v-if="item.status==2"> </span>
                    </div>
                    <span :class="['code',item.status==3 ? 'color3': '']">{{item.code}}</span>
                </div>
            </li>
        </ul>
        <div class="none_bg need-login" v-if="items.length==0">
            <span class="img bg03"></span>
            <p class="remark">
                您还没有技能证书
            </p>
        </div>
    </v-content>
</template>
<style scoped>
    .section {
        margin-bottom: 10px;
    }

    .section .matter {
        height: .8rem;
    }

    .certificate-box .thumb {
        margin-top: .15rem;
        float: left;
        height: 50px;
        width: 50px;
        background-size: 50px 50px;
        background-image: url("../../../assets/img/cert.png");
    }

    .certificate-box .name {
        margin: .15rem auto auto .15rem;
        font-size: .14rem;
        line-height: .14rem;
        color: #333;
    }

    .certificate-box .code {
        font-size: .12rem;
        line-height: .12rem;
        position: absolute;
        top: 0.37rem;
        left: .65rem;
        color: #666;
    }

    .certificate-box .status {
        font-size: .12rem;
    }

    .certificate-box .color0 {
        color: #1eb9ff;
    }

    .certificate-box .color1 {
        color: #78dc23;
    }

    .certificate-box .color2 {
        color: #fa3c46;
    }

    .certificate-box .color3 {
        color: #999999;
    }

    .img.help {
        width: .15rem;
        height: .15rem;
        zoom: .5;
        display: inline-block;
        vertical-align: middle;
        background: url(../../../assets/img/icon/help03.png);
    }

    .img.bg03 {
        background-image: url(../../../assets/img/bg/null_icon03.png);
    }
</style>
<script>
    import {url, lt} from '../../../const'
    import {imgHost} from '../../../filter'
    import {toast} from '../../../assets/js/toast'
    import VHeader from '../../../components/Header'
    import VContent from '../../../components/Content'
    import $ from 'jquery'
    export default{
        route: {
            data({from, to}){
                let vm = this;
                this.$http.get(url.getCertifications).then(
                    function ({data:{success, data, resultMsg}}) {
                        if (success) {
                            this.items = [];
                            let items = data;
                            for (let i in items) {
                                this.items.push(items[i]);
                            }
                        } else {
                            toast.alert(resultMsg);
                        }
                    }, function (response) {
                        toast.alert('网络异常');
                    }
                );
            }
        },
        data(){
            return {
                items: []
            }
        },
        ready(){

        },
        methods: {
            showRemark (event, status){
                if (status != 2) {
                    return;
                }
                event.preventDefault();
                toast.message('审核失败', '审核失败');
            }
        },
        components: {VHeader, VContent}
    }
</script>



